@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family:  "Courier New",
    monospace;
}

button {
  cursor:pointer;
}
.particles {
  position:fixed;
  top: 0;
  left:0;
  right:0;
  bottom:0;
  z-index: -1;
}

header {
  position: relative;
  overflow: hidden;
  padding-bottom:1rem;
}
header nav {
  position: absolute;
  right:0;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

.form {
  max-width: 700px;

}
.image-container { 
  -webkit-transition: all .3s ease; 
  transition: all .3s ease;
  position: relative;
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #f21432 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.bounding-box:hover {
  box-shadow:0 0 0 3px black inset;
}
.logo {
  top:0;
  left: 0;
  display: block;
  float: left;
}
.skull {
  width: 90px;
  padding:30px
}
